/* Reset default margins and ensure full-page black background */
body {
    margin: 0;
    padding: 0;
    background-color: #1a1a1a; /* Near-black for depth */
    color: #ffffff; /* Default text color */
    font-family: 'Arial', sans-serif;
  }
  
  /* Container styling */
  .timetable-container {
    padding: 20px;
    background-color: #1a1a1a;
    max-width: 1200px;
    margin: 0 auto;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5); /* Subtle shadow for depth */
  }
  
  /* Header bar styling */
  .header-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background: linear-gradient(90deg, #ffd700, #ffeb3b); /* Yellow gradient */
    color: #1a1a1a; /* Dark text for contrast */
    border-radius: 8px 8px 0 0;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .timezone-box {
    font-size: 14px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 5px 10px;
    border-radius: 5px;
  }
  
  /* FullCalendar customizations */
  .fc {
    background-color: #2a2a2a; /* Slightly lighter black for calendar */
    border-radius: 0 0 8px 8px;
    overflow: hidden;
  }
  
  /* Header toolbar (prev, next, title) */
  .fc .fc-toolbar {
    background: #ffd700; /* Solid yellow */
    color: #1a1a1a;
    padding: 10px;
    border-bottom: 2px solid #ffeb3b; /* Lighter yellow border */
  }
  
  .fc .fc-toolbar-title {
    font-size: 20px;
    font-weight: 600;
  }
  
  .fc .fc-button {
    background-color: #ffeb3b; /* Light yellow */
    border: none;
    color: #1a1a1a;
    font-weight: bold;
    text-transform: uppercase;
    padding: 5px 15px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .fc .fc-button:hover {
    background-color: #ffffff; /* White on hover */
  }
  
  /* Day headers (e.g., Wed 5, Thu 6) */
  .fc .fc-col-header-cell {
    background-color: #333333; /* Dark gray */
    color: #ffd700; /* Yellow text */
    font-weight: 600;
    padding: 10px;
    border-bottom: 1px solid #ffd700;
  }
  .fc-col-header-cell > table{
    margin-bottom: 0!important;
  }
  .fc .fc-col-header-cell .fc-daygrid-day-number {
    font-size: 16px;
  }
  
  /* Time slots (left column) */
  .fc .fc-timegrid-slot-label {
    color: #ffffff;
    font-size: 14px;
    border-color: #444444; /* Subtle gray border */
  }
  
  /* Available slot events */
  .fc .fc-event {
    background-color: #90ee90; /* Light green as per previous code */
    border: 2px solid #ffd700; /* Yellow border */
    color: #1a1a1a; /* Dark text for readability */
    font-size: 14px;
    font-weight: 500;
    border-radius: 5px;
    padding: 2px 5px;
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .fc .fc-event:hover {
    transform: scale(1.05); /* Slight zoom on hover */
    box-shadow: 0 2px 10px rgba(255, 215, 0, 0.5); /* Yellow glow */
  }
  
  /* Grid lines */
  .fc .fc-timegrid-slot {
    background-color: #2a2a2a;
    border-color: #444444;
  }
  
  .fc .fc-timegrid-col {
    background-color: #2a2a2a;
  }
  
  /* Background for unavailable slots (optional) */
  .fc .fc-timegrid-col.fc-day-today {
    background-color: #333333; /* Slightly lighter for "today" */
  }
  
  /* Ensure consistency across days */
  .fc .fc-daygrid-day-frame {
    background-color: #2a2a2a;
  }
