/* Backdrop with a subtle blur effect */
.course-overlay-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(8px); /* Adds a blur effect to the backdrop */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in-out;
}

/* Overlay Content with a more refined gradient and glassmorphism effect */
.course-overlay-content {
  background: linear-gradient(135deg, rgba(252, 213, 53, 0.9), rgba(0, 0, 0, 0.9));
  color: white;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  padding: 2.5rem;
  position: relative;
  overflow-y: auto;
  animation: slideUp 0.5s ease;
  scrollbar-width: thin;
  scrollbar-color: #fcd535 #000;
  border: 1px solid rgba(255, 255, 255, 0.1); /* Adds a subtle border */
}

/* Close Button with a glowing hover effect */
.course-close-button {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: none;
  color: white;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  transition: transform 0.3s ease, color 0.3s ease;
}

.course-close-button:hover {
  color: #fcd535;
  transform: rotate(90deg) scale(1.1);
}

/* Header with a gradient text effect */
.overlay-header h2 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  background: linear-gradient(135deg, #fcd535, #fff200);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
}

.overlay-header p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.9);
}

/* Details Section with a subtle border and padding */
.details-section {
  margin-top: 1.5rem;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.details-section h3 {
  font-size: 1.5rem;
  color: #fcd535;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.details-section p,
.details-section ul {
  font-size: 1rem;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.9);
}

.details-section ul {
  list-style: disc;
  margin-left: 1.5rem;
}

/* CTA Button with a glowing hover effect */
.overlay-cta {
  text-align: center;
  margin-top: 2rem;
}

.enroll-button {
  background: #fcd535;
  color: #000;
  font-size: 1.2rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(252, 213, 53, 0.4);
}

.enroll-button:hover {
  background: #fff200;
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(252, 213, 53, 0.6);
}

/* Media Queries for Smaller Screens */
@media (max-width: 768px) {
  .course-overlay-content {
    padding: 1.5rem;
    width: 95%;
  }

  .overlay-header h2 {
    font-size: 1.6rem;
  }

  .details-section h3 {
    font-size: 1.3rem;
  }

  .enroll-button {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
}

/* Scrollbar Styling */
.course-overlay-content::-webkit-scrollbar {
  width: 8px;
}

.course-overlay-content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
}

.course-overlay-content::-webkit-scrollbar-thumb {
  background-color: #fcd535;
  border-radius: 8px;
  border: 2px solid rgba(0, 0, 0, 0.3);
}

/* Keyframes for animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}