.my-card {
    background-color: #1a1a1a;
    border: 1px solid #333;
    border-radius: 0.5rem;
    color: #fff;
    position: relative;
    transition: transform 0.2s ease-in-out, border-color 0.2s ease-in-out;
    overflow: hidden;
  }
  
  .my-card:hover {
    transform: translateY(-3px);
    border-color: #fcd535;
  }
  