/* ----------------------------------------------
   1) Root Variables (Gold-on-Black Theme)
---------------------------------------------- */
:root {
  --bg-color: #000000;         /* Main page background */
  --card-bg: #1a1a1a;          /* Card background */
  --border-color: #333333;     /* Border color */
  --primary-color: #fcd535;    /* Gold accent color */
  --text-color: #ffffff;       /* Primary text color */
  --muted-color: #cccccc;      /* Muted text */
  --green-color: #48bb78;      /* For 'completed' status or success */
  --radius-md: 0.5rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --transition-fast: 0.2s ease-in-out;
}
.campaign-body{
  background-color: rgba(0, 0, 0, 0.9)
}
/* Container */
.my-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: var(--spacing-lg) var(--spacing-md);
  
}

/* ----------------------------------------------
   2) Page Titles
---------------------------------------------- */
.page-title {
  font-size: 2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
    text-transform: uppercase;
    /* font-size: 2rem; */
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
    color: #ffe8ad;
    text-align: center;
}

.page-subtitle {
  font-size: 1rem;
  color: var(--muted-color);
  margin-bottom: var(--spacing-lg);
  text-align: center;
}

/* ----------------------------------------------
   3) Error Box
---------------------------------------------- */
.error-box {
  margin: var(--spacing-lg) auto;
  padding: var(--spacing-md);
  max-width: 600px;
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  text-align: center;
}

.error-text {
  color: #f56565; /* A red shade for errors */
  font-weight: 600;
  font-size: 1.125rem;
}

/* ----------------------------------------------
   4) Basic Button (MyButton)
---------------------------------------------- */
.my-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-color);
  color: #000;
  font-weight: 600;
  border: none;
  border-radius: var(--radius-md);
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: background-color var(--transition-fast),
              transform var(--transition-fast);
}

.my-button:hover {
  background-color: rgba(252, 213, 53, 0.9);
  transform: translateY(-1px);
}

.my-button.disabled,
.my-button:disabled {
  background-color: #666;
  cursor: not-allowed;
  transform: none;
}

/* ----------------------------------------------
   5) Links (MyLink)
---------------------------------------------- */
.my-link {
  transition: color var(--transition-fast);
}
.my-link:hover {
  color: var(--primary-color);
}
.my-link-disabled {
  color: #666;
  cursor: not-allowed;
}

/* ----------------------------------------------
   6) Card (MyCard)
---------------------------------------------- */
.my-card {
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  color: var(--text-color);
  transition: transform var(--transition-fast),
              border-color var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.my-card:hover {
  transform: translateY(-3px);
  border-color: var(--primary-color);
}

/* ----------------------------------------------
   7) Top "Next Lesson" & "Next Workbook" Cards
---------------------------------------------- */
.lesson.completed {
  background-color: #e6ffed;  /* a light green background */
  color: #2e7d32;            /* a dark green text (optional) */
}
.lesson.completed .completed-icon {
  color: #2e7d32;            /* green icon color to match, if needed */
}


.cards-row {
  display: grid;
  gap: var(--spacing-md);
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin-bottom: var(--spacing-lg);
}

.next-item-card {
  cursor: pointer;
  overflow: hidden;
}

/* Background image overlay inside a card */
.card-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 0; /* Sends it to the background */
}
.card-bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.card-bg-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4); /* Dark overlay for contrast */
}

/* Card content layout */
.campaign-card-content {
  position: relative;
  z-index: 1;
  display: flex  ;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  color: white;
}

.campaign-card-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-md);
}

.icon-circle {
  width: 48px;
  height: 48px;
  background-color: rgba(252, 213, 53, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.campaign-card-title {
  font-weight: 600;
  font-size: 1.125rem;
}

.campaign-card-subtitle {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.7);
}

.card-main-text {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  color: white;
}

.card-desc {
  color: var(--muted-color);
  margin-bottom: var(--spacing-md);
  font-size: 0.875rem;
}

/* ----------------------------------------------
   8) Tabs
---------------------------------------------- */
.tabs-section {
  margin-bottom: var(--spacing-lg);
}

.my-tabs {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-md);
  margin-top: 3rem;
}

.my-tabs-list {
  display: flex;
  gap: var(--spacing-md);
  border-bottom: 1px solid var(--border-color);
}

.my-tabs-trigger {
  background: none;
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 500;
  color: var(--muted-color);
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: color var(--transition-fast);
}

.my-tabs-trigger.active {
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.my-tabs-content {
  padding-top: var(--spacing-md);
}

/* ----------------------------------------------
   9) Module Grid (Course Content)
---------------------------------------------- */
.module-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 1fr;
}

.campaign-module-card {
  cursor: pointer;
  overflow: hidden;
  padding: 0; /* Let the image fill the card */
}

.campaign-module-card:hover .module-image {
  transform: scale(1.05);
}

.module-image {
  width: 100%;
  object-fit: cover;
  transition: transform var(--transition-fast);
  display: block;
}

.module-image-placeholder {
  width: 100%;
  height: 160px;
  background-color: #444;
  display: flex;
  align-items: center;
  justify-content: center;
}

.module-info {
  padding: var(--spacing-md);
}

.module-num {
  display: block;
  font-size: 0.875rem;
  color: var(--primary-color);
  margin-bottom: 0.25rem;
}

.module-title {
  font-size: 1rem;
  font-weight: 600;
}

/* ----------------------------------------------
   10) List View (Lessons & Workbooks)
---------------------------------------------- */
.list-view {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.list-item {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-sm);
  padding: var(--spacing-sm);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: background-color var(--transition-fast);
}

.list-item:hover {
  background-color: rgba(252, 213, 53, 0.1);
}

.list-item.locked {
  opacity: 0.4;
  pointer-events: none;
}

.list-item.completed {
  border-color: var(--green-color);
  background-color: rgba(72, 187, 120, 0.1);
}

.list-item.next {
  border-color: var(--primary-color);
}

.list-thumb {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: var(--radius-md);
}

.list-text {
  display: flex;
  flex-direction: column;
  flex: 1;
  align-self: center;
}

.list-title {
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.list-desc {
  font-size: 0.875rem;
  color: var(--muted-color);
}

/* Badge for "Next Up" */
.my-badge {
  display: inline-block;
  background-color: #444;
  color: var(--text-color);
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius-md);
  font-size: 0.75rem;
  font-weight: 600;
}

.badge-next {
  background-color: var(--primary-color);
  color: #000;
}

/* ----------------------------------------------
   11) Icons
---------------------------------------------- */
.icon-check,
.icon-lock,
.icon-play,
.icon-book,
.icon-x {
  font-weight: bold;
  margin-right: 0.375rem;
}

/* ----------------------------------------------
   12) Module Overlay
---------------------------------------------- */
.module-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-md);
  z-index: 9999;
}

.module-overlay-card {
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  max-width: 800px;
  width: 100%;
  max-height: 90vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
}

.overlay-header {
  position: relative;
  background-color: #333;
}

.overlay-header-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.overlay-header-placeholder {
  width: 100%;
  height: 200px;
  background-color: #444;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay-close {
  position: absolute;
  top: var(--spacing-sm);
  right: var(--spacing-sm);
  background: none;
  border: none;
  color: var(--text-color);
  font-size: 1.25rem;
  cursor: pointer;
  transition: transform var(--transition-fast);
}

.overlay-close:hover {
  transform: scale(1.1);
}

.overlay-header-text {
  position: absolute;
  bottom: var(--spacing-sm);
  left: var(--spacing-sm);
}

.overlay-header-num {
  display: block;
  font-size: 0.875rem;
  color: var(--primary-color);
  margin-bottom: 0.25rem;
}

.overlay-header-title {
  font-size: 1.25rem;
  font-weight: 700;
}

/* Body of overlay, scrollable */
.overlay-body {
  flex: 1;
  overflow-y: auto;
  padding: var(--spacing-md);
}

.overlay-description {
  color: var(--muted-color);
  margin-bottom: var(--spacing-md);
}

.overlay-list {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.overlay-list-item {
  display: flex;
  align-items: center;
  padding: var(--spacing-sm);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: background-color var(--transition-fast);
}

.overlay-list-item:hover {
  background-color: rgba(252, 213, 53, 0.1);
}

.overlay-list-item.completed {
  border-color: var(--green-color);
  background-color: rgba(72, 187, 120, 0.1);
}

.overlay-list-item.locked {
  opacity: 0.4;
  pointer-events: none;
}

.overlay-list-item.next {
  border-color: var(--primary-color);
}

.overlay-item-title {
  margin-left: 0.5rem;
  font-weight: 600;
}

/* Grid Layout for Description */
.overlay-description {
    column-count: 2;  /* Two columns */
    column-gap: 1rem; /* Space between columns */
    max-width: 100%;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.1); /* Glass effect */
    backdrop-filter: blur(10px);
    border-radius: 12px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
  }

/* Individual Description Items */
.overlay-description > div {
  break-inside: avoid; /* Prevents breaking in the middle */
    padding: 1rem;
    border-radius: 8px;
    font-size: 0.95rem;
    font-weight: 500;
    display: inline-block; /* Ensures proper stacking */
    width: 100%; /* Ensures it fills the column width */
    transition: all 0.3s ease-in-out;
}
.overlay-description > div > h2{
  color: #fcd535;
}
/* Hover Effect */
.overlay-description-item:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.03);
}

/* If No Description */
.no-description {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
  text-align: center;
  padding: 1rem;
  font-style: italic;
}

/* Mobile-Friendly Adjustments */
@media (max-width: 768px) {
  .overlay-description-grid {
    grid-template-columns: 1fr; /* Single column on small screens */
    grid-auto-flow: row; /* Default stacking */
  }
}
/* Completed items get a green tint */
.list-item.completed,
.overlay-list-item.completed {
  border-color: #00FF9C;
  color: #FFE700;
}

.completed-icon {
  color: #2e7d32;
}

/* Normal styling for locked and next (adjust as needed) */
.list-item.locked,
.overlay-list-item.locked {
  opacity: 0.5;
  pointer-events: none;
}

.list-item.next,
.overlay-list-item.next {
  border-color: #fcd535;
}

/* Additional styling for cards, lists, overlay etc. */
/* ... (use your previous CSS or customize as desired) */
.list-module-name{
  color: crimson;
}
.wordlist-access-button{
  align-self: center;
    background-color: white;
    border-radius: 50%;
    width: 48px; /* Slightly larger for better visibility */
    height: 48px;
    font-size: 24px; /* Adjusted for better proportion */
    padding: 0.5rem; /* Increased padding for better spacing */
    color: black;
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    transition: all 0.3s ease; /* Smooth transition for hover effects */
    cursor: pointer; /* Indicates clickable */
    border: 2px solid transparent; 
}

.wordlist-access-button:hover {
  background-color: #7C00FE; /* Light gray background on hover */
  color: white; /* Blue text color on hover */
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15); /* Larger shadow on hover */
  border-color: #3b82f6; /* Blue border on hover */
  transform: translateY(-2px); /* Slight lift effect */
}

.wordlist-access-button:active {
  transform: translateY(0); /* Reset lift effect when clicked */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Reset shadow when clicked */
}