}

.chart-container {
  position: relative;
  height: 250px; /* Adjust height as needed */
  width: 100%;
}

/* ---------------------------------------------------------------------------
   Balance Overlay Additional Styles (if not already present)
--------------------------------------------------------------------------- */
.workbook-chart-section {
  margin-bottom: 30px;
}

/* Ensure that other overlay content has ample spacing */
.overlay-content h2 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
  border-bottom: 2px solid #fcd535;
  padding-bottom: 10px;
}

.overlay-content h3 {
  font-size: 1.4rem;
  color: #444;
  margin-top: 20px;
  margin-bottom: 10px;
}
/* ---------------------------------------------------------
   Tailwind-Inspired CSS for a Fancy Profile Setup Modal
   ---------------------------------------------------------
   1) Modal Overlay
   2) Modal Container (with glassy, gradient background)
   3) Form Elements
   4) Animations & Transitions
*/

/* :root variables for easy theming */
:root {
  --tw-color-primary: #3B82F6;          /* Tailwind's "blue-500" */
  --tw-color-primary-hover: #2563EB;   /* Tailwind's "blue-600" */
  --tw-color-gray-300: #D1D5DB;
  --tw-color-gray-700: #374151;
  --tw-color-white: #ffffff;
  --tw-color-black: #000000;

  --tw-modal-bg: rgba(0, 0, 0, 0.35);   /* Overlay background */
}

/* ----------- 1) Modal Overlay ----------- */
.profile-modal-overlay {
  position: fixed;
  inset: 0; /* top:0; right:0; bottom:0; left:0; */
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--tw-modal-bg);
  backdrop-filter: blur(4px); /* Glassmorphism effect */
  z-index: 9999;
}

/* ----------- 2) Modal Container ----------- */
.profile-modal-container {
  position: relative;
  width: 100%;
  max-width: 30rem; /* ~ Tailwind's max-w-md */
  padding: 2rem;    /* ~ Tailwind's p-8 */
  border-radius: 0.75rem; /* ~ Tailwind's rounded-lg */
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25); /* Larger drop shadow */
  background: linear-gradient(to bottom right, #ffffff 60%, #f0f4f8);
  animation: scaleUp 0.3s ease forwards;
}

/* Subtle "scale in" animation for the modal */
@keyframes scaleUp {
  0% {
    transform: scale(0.95);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Close icon if desired */
.profile-modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 1.5rem;
  color: var(--tw-color-gray-700);
  cursor: pointer;
  transition: color 0.2s ease;
}
.profile-modal-close:hover {
  color: var(--tw-color-black);
}

/* ----------- 3) Form Elements ----------- */
.profile-modal-container h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;        /* ~ Tailwind's text-2xl */
  font-weight: 700;         /* ~ Tailwind's font-bold */
  color: #1F2937;           /* text-gray-800 */
}

.profile-form-group {
  margin-bottom: 1rem; /* ~ Tailwind's mb-4 */
}

.profile-form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.875rem; /* ~ Tailwind's text-sm */
  font-weight: 500;    /* ~ Tailwind's font-medium */
  color: var(--tw-color-gray-700);
}

/* Inputs & selects with a Tailwind-like focus ring */
.profile-input,
.profile-select {
  width: 100%;
  padding: 0.5rem 0.75rem; /* ~ Tailwind's p-2 or p-3 */
  border: 1px solid var(--tw-color-gray-300);
  border-radius: 0.375rem; /* ~ Tailwind's rounded-md */
  background-color: var(--tw-color-white);
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  outline: none;
}

/* Emulate Tailwind's focus ring effect */
.profile-input:focus,
.profile-select:focus {
  border-color: var(--tw-color-primary);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2); /* "blue-500" ring offset */
}

.profile-error {
  margin-top: 0.5rem;
  font-size: 0.75rem; /* ~ Tailwind's text-xs */
  color: #EF4444;     /* ~ Tailwind's text-red-500 */
}

/* ----------- 4) Button Styles & Hover Effects ----------- */
.profile-submit-btn {
  display: block;
  width: 100%;
  padding: 0.75rem;
  margin-top: 0.5rem;
  text-align: center;
  font-weight: 600;
  background-color: var(--tw-color-primary);
  color: var(--tw-color-white);
  border-radius: 0.5rem; /* ~ Tailwind's rounded */
  transition: background-color 0.2s ease, transform 0.1s ease;
  cursor: pointer;
  border: none;
}

.profile-submit-btn:hover {
  background-color: var(--tw-color-primary-hover);
  transform: translateY(-1px);
}

.profile-submit-btn:active {
  transform: translateY(1px);
}

/* (Optional) Subtle fade-in for the overlay itself */
@keyframes fadeOverlay {
  from { background-color: rgba(0, 0, 0, 0); }
  to   { background-color: var(--tw-modal-bg); }
}



